"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Alert, AlertDescription } from "@/components/ui/alert";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import {
  FileText,
  Download,
  MessageSquare,
  Reply,
  ArrowLeft,
} from "lucide-react";
import { CourseFile } from "./types";
import { ResponseDialog } from "@/components/shared/dialogs/ResponseDialog";
import { useState } from "react";

interface ViewFileDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  file: CourseFile | null;
  onDownload: (file: CourseFile) => void;
  onResponse: (response: string) => Promise<void>;
  threadMessages: any[];
  onMessageReply: (message: string) => Promise<void>;
}

export function ViewFileDialog({
  open,
  onOpenChange,
  file,
  onDownload,
  onResponse,
  threadMessages = [],
  onMessageReply,
}: ViewFileDialogProps) {
  const [isResponseOpen, setIsResponseOpen] = useState(false);
  const [isMessageReplyOpen, setIsMessageReplyOpen] = useState(false);

  const handleResponseSubmit = async (response: string) => {
    await onResponse(response);
    setIsResponseOpen(false);
  };

  const handleMessageReplySubmit = async (message: string) => {
    await onMessageReply(message);
    setIsMessageReplyOpen(false);
  };

  return (
    <>
      <Dialog open={open} onOpenChange={onOpenChange}>
        <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto p-0 border-0">
          <DialogHeader className="sr-only">
            <DialogTitle>{file?.fileName || "File Details"}</DialogTitle>
          </DialogHeader>

          {file && (
            <div className="bg-white">
              {/* Header with file info */}
              <div className="px-6 py-4 border-b">
                <div className="flex items-start justify-between gap-4">
                  <div className="flex-1">
                    <div className="flex items-center gap-2 mb-2">
                      <FileText className="h-5 w-5 text-gray-600" />
                      <h2 className="text-2xl font-bold">{file.fileName}</h2>
                    </div>
                    <div className="flex items-center gap-3 flex-wrap">
                      <span className="text-sm text-gray-600">
                        {file.courseCode} - {file.courseName}
                      </span>
                      <Badge variant="outline">{file.fileType}</Badge>
                      <Badge
                        className={
                          file.status === "Approved"
                            ? "bg-green-100 text-green-800"
                            : file.status === "Rejected"
                              ? "bg-red-100 text-red-800"
                              : "bg-yellow-100 text-yellow-800"
                        }
                      >
                        {file.status || "Pending"}
                      </Badge>
                    </div>
                  </div>
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={() => onOpenChange(false)}
                  >
                    ✕
                  </Button>
                </div>
              </div>

              {/* Document Viewer Section */}
              <div className="px-6 py-6 border-b">
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                      <FileText className="h-5 w-5" />
                      Document Viewer
                    </CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="aspect-16/10 bg-gray-100 rounded-lg flex items-center justify-center border-2 border-dashed border-gray-300">
                      <div className="text-center">
                        <FileText className="h-12 w-12 text-gray-400 mx-auto mb-2" />
                        <p className="text-sm text-gray-600">
                          Document: {file.fileName}
                        </p>
                        <p className="text-xs text-gray-500 mt-2">
                          Live document preview would appear here
                        </p>
                        <Button
                          variant="outline"
                          size="sm"
                          className="mt-4"
                          onClick={() => onDownload(file)}
                        >
                          <Download className="h-4 w-4 mr-2" />
                          Download Document
                        </Button>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              </div>

              {/* File Details */}
              <div className="px-6 py-6 border-b">
                <h3 className="text-lg font-semibold mb-4">File Details</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="bg-gray-50 p-4 rounded-lg">
                    <p className="text-xs text-gray-500 uppercase tracking-wider">
                      Course Code
                    </p>
                    <p className="text-lg font-medium mt-1">
                      {file.courseCode}
                    </p>
                  </div>
                  <div className="bg-gray-50 p-4 rounded-lg">
                    <p className="text-xs text-gray-500 uppercase tracking-wider">
                      Course Name
                    </p>
                    <p className="text-lg font-medium mt-1">
                      {file.courseName}
                    </p>
                  </div>
                  <div className="bg-gray-50 p-4 rounded-lg">
                    <p className="text-xs text-gray-500 uppercase tracking-wider">
                      File Type
                    </p>
                    <p className="text-lg font-medium mt-1">{file.fileType}</p>
                  </div>
                  <div className="bg-gray-50 p-4 rounded-lg">
                    <p className="text-xs text-gray-500 uppercase tracking-wider">
                      Semester
                    </p>
                    <p className="text-lg font-medium mt-1">
                      {file.semester} {file.academicYear}
                    </p>
                  </div>
                  <div className="bg-gray-50 p-4 rounded-lg">
                    <p className="text-xs text-gray-500 uppercase tracking-wider">
                      Upload Date
                    </p>
                    <p className="text-lg font-medium mt-1">
                      {file.uploadDate}
                    </p>
                  </div>
                  <div className="bg-gray-50 p-4 rounded-lg">
                    <p className="text-xs text-gray-500 uppercase tracking-wider">
                      File Size
                    </p>
                    <p className="text-lg font-medium mt-1">{file.size}</p>
                  </div>
                </div>
              </div>

              {/* Admin Review Section */}
              {file.status && (
                <div className="px-6 py-6 border-b">
                  <h3 className="text-lg font-semibold mb-4 flex items-center gap-2">
                    <MessageSquare className="h-5 w-5 text-gray-600" />
                    Admin Review
                  </h3>

                  {file.adminRemarks ? (
                    <div className="space-y-3">
                      <Alert
                        className={
                          file.status === "Approved"
                            ? "border-green-200 bg-green-50"
                            : file.status === "Rejected"
                              ? "border-red-200 bg-red-50"
                              : "border-yellow-200 bg-yellow-50"
                        }
                      >
                        <AlertDescription>
                          <p className="text-sm mb-3">{file.adminRemarks}</p>
                          {file.reviewedBy && (
                            <div className="text-xs text-gray-600 pt-2 border-t border-gray-200">
                              <p>Reviewed by: {file.reviewedBy}</p>
                              {file.reviewedDate && (
                                <p>Date: {file.reviewedDate}</p>
                              )}
                            </div>
                          )}
                        </AlertDescription>
                      </Alert>

                      {/* Faculty Response */}
                      {file.facultyResponse ? (
                        <Alert className="border-blue-200 bg-blue-50">
                          <AlertDescription>
                            <p className="text-xs text-blue-800 mb-2">
                              Your Response:
                            </p>
                            <p className="text-sm mb-3">
                              {file.facultyResponse}
                            </p>
                            {file.responseDate && (
                              <div className="text-xs text-gray-600 pt-2 border-t border-gray-200">
                                <p>Response Date: {file.responseDate}</p>
                              </div>
                            )}
                          </AlertDescription>
                        </Alert>
                      ) : (
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => setIsResponseOpen(true)}
                          className="w-full"
                        >
                          <Reply className="h-4 w-4 mr-2" />
                          Respond to Admin Review
                        </Button>
                      )}
                    </div>
                  ) : (
                    <Alert>
                      <AlertDescription className="text-sm text-gray-500">
                        This file is pending admin review. You will be notified
                        once the review is complete.
                      </AlertDescription>
                    </Alert>
                  )}
                </div>
              )}

              {/* Auditor Messages Section */}
              {threadMessages && threadMessages.length > 0 && (
                <div className="px-6 py-6 border-b">
                  <h3 className="text-lg font-semibold mb-4 flex items-center gap-2">
                    <MessageSquare className="h-5 w-5 text-gray-600" />
                    Auditor Messages
                    <Badge variant="outline">{threadMessages.length}</Badge>
                  </h3>
                  <div className="space-y-3 max-h-[40vh] overflow-y-auto pr-1">
                    {threadMessages.map((msg) => (
                      <div
                        key={msg.id}
                        className="rounded-md border bg-white p-3"
                      >
                        <div className="flex items-center justify-between text-xs text-gray-500">
                          <span>
                            {msg.senderName || msg.senderRole || "Message"}
                          </span>
                          {msg.createdAt && (
                            <span>
                              {new Date(msg.createdAt).toLocaleString()}
                            </span>
                          )}
                        </div>
                        <p className="text-sm text-gray-700 mt-2">
                          {msg.message}
                        </p>
                      </div>
                    ))}
                  </div>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => setIsMessageReplyOpen(true)}
                    className="mt-3 w-full"
                  >
                    <Reply className="h-4 w-4 mr-2" />
                    Reply to Auditor
                  </Button>
                </div>
              )}

              {/* Action Buttons */}
              <div className="px-6 py-6 bg-gray-50 border-t flex gap-2 rounded-b-lg">
                <Button onClick={() => onDownload(file)} className="flex-1">
                  <Download className="h-4 w-4 mr-2" />
                  Download File
                </Button>
                <Button variant="outline" onClick={() => onOpenChange(false)}>
                  Close
                </Button>
              </div>
            </div>
          )}
        </DialogContent>
      </Dialog>

      {/* Response Dialogs */}
      <ResponseDialog
        open={isResponseOpen}
        onOpenChange={setIsResponseOpen}
        onSubmit={handleResponseSubmit}
        itemType="file"
      />
      <ResponseDialog
        open={isMessageReplyOpen}
        onOpenChange={setIsMessageReplyOpen}
        onSubmit={handleMessageReplySubmit}
        itemType="file"
      />
    </>
  );
}
